@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body{margin:0;font-family:'Montserrat', sans-serif !important;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.about-card{padding:1rem;max-width:50rem;text-align:center}.about-skills{display:flex;flex-wrap:wrap;justify-content:center}.about-skills-title{padding-top:2rem;font-weight:500;font-style:italic}.about-description{font-weight:400;font-size:1.2rem}.about-icon{margin:1rem}body.light-mode{background-color:#fff;color:#212529;transition:background-color 0.3s ease}body.dark-mode{background-color:#232935;color:#fff;transition:background-color 0.3s ease}button{outline:none}button:focus{outline:0 !important}.a-blue{color:#00aeff;text-decoration:none}.a-blue:hover{color:#00aeff;text-decoration:underline}.btn-icon{background-color:transparent;border:none}.btn-icon:hover{color:#00aeff !important}.section{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:2rem;padding-top:4rem;padding-bottom:2rem}.section-title{font-weight:600;border-bottom:4px solid #00aeff}.footer{background-color:rgba(0,0,0,0.03);margin-top:2rem;padding-top:2rem;padding-bottom:1rem;text-align:center}.navbar{background:linear-gradient(to bottom, white 0%, rgba(0,0,0,0) 100%)}.navbar.dark-mode{background:linear-gradient(to bottom, #232935 0%, rgba(0,0,0,0) 100%)}.nav-link{position:relative;font-weight:500;text-decoration:none}.nav-link:before,.nav-link:after{content:'';position:absolute;bottom:2px;left:0;right:0;height:3px;background-color:#00aeff}.nav-link:before{opacity:0;transform:translateY(-8px);transition:transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275),opacity 0s}.nav-link:after{opacity:0;transform:translateY(4px);transition:transform .2s cubic-bezier(0.175, 0.885, 0.32, 1.275),opacity .2s}.nav-link:hover:before,.nav-link:hover:after,.nav-link:focus:before,.nav-link:focus:after{opacity:1;transform:translateY(0)}.nav-link:hover:before,.nav-link:focus:before{transition:transform .2s cubic-bezier(0.175, 0.885, 0.32, 1.275),opacity .2s}.nav-link:hover:after,.nav-link:focus:after{transition:transform 0s .2s cubic-bezier(0.175, 0.885, 0.32, 1.275),opacity 0s .2s}.home-section{height:100vh;display:flex;justify-content:center;align-items:center;text-align:center}.home-title{font-weight:500;text-shadow:0 1px #05377048}.home-title span{color:#00aeff}.home-rotating-text{font-weight:300}.home-buttons{display:flex;justify-content:center}.home-image{padding:2rem;max-width:25rem}@media (max-width: 576px){.home-image{max-width:20rem}}.project-flex{display:flex;flex-wrap:wrap;justify-content:center;align-items:center}.project-card-deck{display:flex;flex-wrap:wrap;justify-content:center;text-align:left;max-width:70rem}.project-card-light{margin:1rem;max-width:30rem;color:#212529;box-shadow:0 1px 1px 0 rgba(0,0,0,0.06),0 2px 5px 0 rgba(0,0,0,0.2)}.project-card-dark{margin:1rem;max-width:30rem;background-color:#2d3542 !important;color:white;box-shadow:0 1px 1px 0 rgba(0,0,0,0.06),0 2px 5px 0 rgba(0,0,0,0.2)}.project-card-img{object-fit:cover;max-height:15rem}.project-card-title{font-weight:600 !important;font-style:italic}.project-card-text{font-weight:400;font-size:1.2rem}.project-button{float:right;padding:0 !important}

